import { __spreadArrays } from "tslib";
import Vue from 'vue';
import Router from 'vue-router';
// tslint:disable-next-line
var generatedRoutes = require('../../.rdvue/routes.js').default;
Vue.use(Router);
export var Page;
(function (Page) {
    Page["Home"] = "home";
    Page["Hello"] = "hello-world";
    Page["NotFound"] = "not-found";
})(Page || (Page = {}));
export default new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: __spreadArrays(generatedRoutes, [
        {
            path: '/',
            name: Page.Hello,
            meta: {
                layout: 'default',
            },
            component: function () {
                return import(
                /* webpackChunkName: "hello-world" */
                '@/pages/hello-world');
            },
        },
        {
            path: '*',
            name: Page.NotFound,
            meta: {
                layout: 'default',
            },
            component: function () {
                return import(
                /* webpackChunkName: "not-found" */
                '@/pages/not-found');
            },
        },
        {
            path: '/home',
            name: Page.Home,
            meta: {
                layout: 'default',
            },
            component: function () {
                return import(
                /* webpackChunkName: "not-found" */
                '@/pages/twitter');
            },
        },
    ]),
});
